/**
 * Nav dropdown
 *
 */
.nav-dropdown {
  list-style-type: none;
  margin: 0 0 20px 0;
  padding: 0;
  font-size: .85em;
  width: 100%;
}
@media (min-width: 768px) {
  .nav-dropdown {
    visibility: hidden;
    opacity: 0;
    box-shadow: 0 2px 7px rgba(0, 0, 0, 0.2);
    position: absolute;
    top: 20px;
    left: 50%;
    padding: 10px 0;
    margin: 0;
    transform: translateX(-50%) scale(0.96);
    transition: all .12s ease;
    width: auto;
    z-index: 1000;
  }
  .nav-section__item.hover .nav-dropdown,
  .nav-section__item:hover .nav-dropdown {
    visibility: visible;
    opacity: 1;
    top: 40px;
  }
}
.nav-dropdown__item > a {
  text-transform: uppercase;
  display: inline-block;
  text-decoration: none;
  font-size: 0.8125rem;
  font-weight: 600;
  letter-spacing: .05em;
  line-height: 1em;
  color: #101010;
}
/**
 * Nav section
 *
 */
.nav-section {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .nav-section {
    flex-direction: row;
  }
}
.nav-section__item {
  display: block;
  margin-bottom: 10px;
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .nav-section__item {
    display: inline-block;
    padding: 0 8px;
    margin-bottom: 0;
    text-align: left;
    position: relative;
    width: auto;
  }
}
@media (min-width: 992px) {
  .nav-section__item {
    padding: 0 15px;
  }
}
.nav-section__item > a {
  display: inline-block;
  border-bottom: 1px transparent solid;
  color: #101010;
  font-weight: 500;
  line-height: 1em;
  padding-top: 8px;
  padding-bottom: 8px;
  transition: color .12s ease;
  text-decoration: none;
}
.nav-section__item:hover > a,
.nav-section__item--is-active > a {
  border-bottom: 1px currentColor solid;
}
/**
 * Nav toggle
 *
 */
.nav-toggle-wrapper {
  padding: 15px;
  line-height: 100%;
}
@media (min-width: 768px) {
  .nav-toggle-wrapper {
    display: none;
  }
}
.nav-toggle:active,
.nav-toggle:focus,
.nav-toggle:hover,
.nav-toggle {
  text-decoration: none;
}
.nav-toggle {
  display: table;
  width: 100%;
}
.nav-toggle__icon,
.nav-toggle__menu {
  display: table-cell;
  vertical-align: top;
  padding: 0;
}
.nav-toggle__icon {
  padding-left: 10px;
  text-align: right;
}
.nav-toggle__menu {
  text-transform: uppercase;
}
/**
 * Nav
 *
 */
.nav {
  border: none;
  border-radius: 0;
  margin-bottom: 20px;
}
